

import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ProductRegistrationService } from 'src/app/service/product-registration.service';
import { Location } from '@angular/common';
import { ISubscription } from "rxjs/Subscription";
import { DataService } from 'src/app/service/data.service';

@Component({
  selector: 'app-add-registeration',
  templateUrl: './add-registeration.component.html'
})
export class AddRegisterationComponent implements OnInit {

  PageTitle = "Product Registration"
  addNewRadio: boolean = false
  loadingState = false;
  addForm: FormGroup;
  productListArray: Array<any> = [];
  orderListArray: Array<any> = [];
  showLoader = false;
  formErrors = {
    apierror: null,
    qty: null
  };
  private subscription: ISubscription;
  isEditing = false;
  editId = null;
  OrderId: string;
  OrderId_show: boolean = true;
  OrderDetails: any;

  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private fb: FormBuilder,
    private location: Location,
    private dataService: DataService,
    private productRegistrationService: ProductRegistrationService,
  ) {

    // console.log("location.path() ==>", location.path());

    this.addForm = this.fb.group({
      order_id: [null, Validators.compose([Validators.required])],
      tag_id: [null, Validators.compose([Validators.required])],
      serial_no: [null, Validators.compose([Validators.required])],
      barcode: [null, Validators.compose([Validators.required])],
      received_qty: [null, Validators.compose([Validators.required])],
      qty: [null, Validators.compose([Validators.required])],
      product_id: [null, Validators.compose([Validators.required])],
      sku_no: [null],
      unload_id: [null],
      unload_detail_id: [null],
    });

    if (this.activatedRoute.snapshot.paramMap.get('id')) {
      let id = this.activatedRoute.snapshot.paramMap.get('id');
      if (location.path() == `/inbound/registeration/add/${id}`) {
        this.OrderId = id;
        this.addForm.patchValue({ order_id: this.OrderId });
        this.OrderId_show = false;
        this.getOrderDetails();
      } else {
        this.editId = id;
        this.isEditing = true;
      }
    }
  }

  ngOnInit() {
    if (this.isEditing) {
      this.getEditObject()
    } else { this.getMasterData(); }
  }

  getMasterData(event = null) {
    event ? this.addForm.patchValue({ product_id: '' }) : '';
    const params: any = {};
    if (this.addForm.value.order_id) {
      params.order_id = this.addForm.value.order_id;
    }
    if (this.isEditing) {
      params.is_editing = this.isEditing;
    }
    this.productRegistrationService.getMasterData(params).subscribe((response) => {
      if (response.success) {
        if (this.addForm.value.order_id) {
          if (this.isEditing) {
            this.orderListArray = response.data.order;
            this.productListArray = response.data.product;
          } else {
            this.productListArray = response.data.product;
          }
        } else {
          this.orderListArray = response.data.order;
        }
      }
    });
  }


  getEditObject() {
    this.productRegistrationService.getProductRegistrationById(this.editId).subscribe((response) => {
      if (response.success) {
        this.addForm.patchValue(response.data);
        this.getMasterData();
      } else {
        // this.router.navigateByUrl('/inbound/registeration');
      }
    });
  }

  setProductDetail(data) {
    if (data) {
      this.addForm.patchValue({
        sku_no: data.sku_no,
        unload_id: data.unload_id,
        unload_detail_id: data.unload_detail_id,
        received_qty: data.received_qty
      });
    }
  }

  qunatitycheck() {
    let qty = Number(this.addForm.value.qty);
    let r_qty = Number(this.addForm.value.received_qty);
    if (qty <= r_qty) {
      this.formErrors.qty = null;
      this.addForm.controls['qty'].setErrors(null);
    }else{
      this.formErrors.qty = "* Please Enter equval or small value in Received Qunatity";
      this.addForm.controls['qty'].setErrors({'incorrect': true});
    }

  }

  submitForm(formData: any): void {
    if (formData.valid) {
      this.showLoader = true;
      if (this.isEditing) {
        this.productRegistrationService.editProductRegistration(this.editId, formData.value).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            this.back();
            // this.router.navigateByUrl('/inbound/registeration');
          }
        }, (error) => {
          this.showLoader = false;
        });
      } else {
        this.productRegistrationService.addProductRegistration(formData.value).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            this.back();
            // this.router.navigateByUrl('/inbound/registeration');
          }
        }, (error) => {
          this.showLoader = false;
        });
      }
    }
  }

  back() {
    this.location.back();
  }

  getOrderDetails() {
    this.subscription = this.dataService.OrderDetails.subscribe((data) => {
      if (data) {
        this.OrderDetails = data;
      }
    });
  }

  ngOnDestroy() {
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
  }

}
