import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { AddRegisterationComponent } from './add-registeration.component';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { FormValidationModule } from 'src/app/shared/form-validation/form-validation.module';
import { NgSelectModule } from '@ng-select/ng-select';
import { ProductRegistrationService } from 'src/app/service/product-registration.service';
import { SharedModule } from 'src/app/shared/shared.module';

const routes: Routes = [
  {
    path: '',
    component: AddRegisterationComponent
  },
  {
    path: ':id',
    component: AddRegisterationComponent
  }
]

@NgModule({
  declarations: [AddRegisterationComponent],
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    FormValidationModule,
    NgSelectModule,
    SharedModule,
    RouterModule.forChild(routes)
  ], providers: [ProductRegistrationService]
})
export class AddRegisterationModule { }


